import common from '../../common/common.js'
import util from '../../common/util.js'
import uniPopup from '../../components/uni-popup.vue'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userid:'',
			shopid:0,
			userInfo:[],
			coupon_nums:0,
			ads:[],
			popType: 'middle',
			showPopupMiddle: false,
			info:{
				bg_color:'0,170,250',
				bg_alpha:1,
				delta:2,
				icon_color:'white',
				shpid:0,
			},
			menu:{
				mindex:5,
				shopid:0,
				shop:[],
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			
			canbuy:1,
		}
	},
	onLoad(option) {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		
		that.userid = userid
		that.userInfo = userInfo
		
		var shopid = option.id
		that.shopid = shopid
		that.menu.shopid = shopid
		that.info.shopid = shopid
		
		that.getShopInfo();
		

		that.getInviteAds();
		
		var currentPages = getCurrentPages()
		that.info.delta = currentPages.length
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		console.log("userid",userid);
		console.log("userInfo",userInfo);
	},
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  console.log('下拉刷新中...');	
	  that.getUserInfo();
	  that.getShopInfo();
	  that.getInviteAds();
	  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	methods: {
		//获取店铺信息
		getShopInfo(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopInfo',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.shop = res.data.shop
					that.menu.shop = res.data.shop
					var shop = res.data.shop
					uni.setNavigationBarTitle({
					    title: shop.shop_title
					});
					console.log(shop)
					that.info.title = shop.shop_title
					that.info.shopid = shop.shop_id










				},
				fail: () => {},
				complete: () => {}
			});
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.showPopupMiddle = true;
		},
		//邀请广告
		getInviteAds(){
			var that = this
			uni.request({
				url: common.apiUrl + 'ads/getInviteAds',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
					that.ads = res.data.ads
					that.canbuy = res.data.canbuy
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//客服
		kefu(){
			var that = this
			uni.showActionSheet({
				itemList: ['在线客服', '0573-82082008'],
				success: function (res) {
					if(res.tapIndex == 0){
						console.log('在线客服');
						that.showMiddlePopup();
					}
					if(res.tabpIndex == 1){
						uni.makePhoneCall({
							phoneNumber:'114'
						})
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		ruzhu(){
			
			uni.navigateToMiniProgram({
			      appId: 'wx8cac4ba732d41675', // 要跳转的小程序的appid
			      path: 'pages/index/index', // 跳转的目标页面
			      extarData: {
			        open: 'auth'
			      },
			      success(res) {
			        // 打开成功  
					console.log('aaa')
			      },
				  fail(res){
					  console.log(res)
				  }
			}) 
		},
		//链接
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url:url
			})
		},
		//获取优惠券数量
		getCouponNums(){
			var that = this
			var userid = uni.getStorageSync('userid');
			
		},
		//获取用户信息
		getUserInfo(){
			var that = this
			var userid = that.userid
			if(userid > 0){
				uni.request({
					url: common.apiUrl+'user/getUserInfo',
					method: 'GET',
					data: {
						userid:userid
					},
					success: res => {
						if(res.data.code == 0){
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo',userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if(res.confirm){
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		
		//用户登录
		checkLogin(){
			var that = this
			var userid = uni.getStorageSync('userid');
			if(!userid){
				console.log('还没有登录');
				uni.navigateTo({
					url: '/pages/userlogin/userlogin',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}else{
				console.log('已经登录');
			}
		},
		
		//用户信息
		setting(){
			uni.navigateTo({
				url: '/pages/user/setting/setting',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	components: {
		uniPopup
	}
}
